.features{
    background-color: #2B076E;
    padding: 100px 0;
    position: relative;
    .card-ill{
        border-radius: 16px;
        background-color: #491F98;
        backdrop-filter: blur(125px);
    }
    .page-title{
        text-align: center;
        max-width: 758px;
        margin: 0 auto 100px;
    }
    .title{
        margin-bottom: 24px;
    }
    .subtitle{
        margin-bottom: 32px;
    }

    .invest{
        padding-bottom: 249px;
        .text-content{
            padding-top: 40px;
            max-width: 454px;
        }
        .row{
            gap: 100px;
        }
        .chart{
            max-width: 605px;
            position: relative;
            font-family: Inter, sans-serif;
            .container-chart{
                width: 100%;
                height: 298px;
                background-color: #35068C;
                border-radius: 16px;
                padding: 40px 40px 48px;
                .chart-info{
                    padding: 0 12px;
                    justify-content: space-between;
                    .img-crypto{
                        max-width: 179px;
                        height: 50px;
                        padding: 0;
                    }
                    p{
                        max-width: 46px;
                        padding: 0;
                        color: #42FFFF;
                        margin: 0;
                        margin-top: 12px;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: normal;
                        letter-spacing: 0.14px;
                    }
                }
                .chart-list{
                    overflow-x: hidden;
                    margin-top: 22px;
                    position: relative;
                    height: 100%;
                    svg{
                        position: absolute;
                        left: 0;
                        transition: 1s;
                        animation-name: svg-chart;
                        animation-duration: 15s;
                        animation-timing-function: linear;
                        animation-iteration-count: infinite;
                    }
                    @keyframes svg-chart {
                        0%{
                            left: 0;
                        }
                        100%{
                            left: calc(-1286px + 501px);
                        }
                    }
                }
            }

            .subimg_content{
                justify-content: start;
                gap: 20%;
                position: absolute;
                bottom: -40%;
                transform: translate(65px, 9px);
                max-width: 580px;
                width: 100%;
                padding: 0 12px;
                .card-bounce{
                    max-width: 178px;
                    width: 100%;
                    height: 184px;
                    padding: 24px 16px;
                    .col{
                        color: var(--text-color);
                        gap: 16px;
                        display: inline-flex;
                        flex-direction: column;
                    }
                    h3{
                        font-size: 20px;
                        font-weight: 500;
                        line-height: 140%;
                        margin: 0;
                    }
                    .row{
                        gap: 6px;
                        padding: 0 12px;
                        align-items: center;
                        svg{
                            max-width: 24px;
                            padding: 0;
                        }
                    }
                    span{
                        max-width: 67px;
                        padding: 0;
                        font-size: 24px;
                        font-weight: 600;
                        line-height: normal;
                        letter-spacing: 1px;
                    }
                    p{
                        margin: 0;
                        font-size: 16px;
                        line-height: normal;
                    }
                }
                .card-visits{
                    max-width: 169px;
                    width: 100%;
                    height: 84px;
                    margin-top: 12px;
                    padding: 16px 32px;
                    color: var(--text-color);
                    text-align: center;
                    span{
                        font-size: 24px;
                        font-weight: 700;
                        line-height: normal;
                        letter-spacing: 0.24px;
                    }
                }
            }
        }
    }

    .detailed-statistics{
        padding-bottom: 120px;
        .img-container{
            max-width: 682px;
            height: 453px;
            position: relative;
            img{
                position: absolute;
                top: 0;
                left: -107px;
            }
        }
        .text-content{
            max-width: 456px;
            padding-top: 105px;
            padding-right: 25px;
            .btn{
                transform: translateX(-2px);
            }
        }
    }

    .grow-profit{
        .row{
            justify-content: space-between;
        }
        .text-content{
            padding-top: 10px;
            max-width: 442px;
        }
        .img-container{
            max-width: 634px;
            font-family: Inter;
            .base{
                z-index: 1;
                position: relative;
                background-color: #35068c;
                border-radius: 16px;
                width: 100%;
                height: 334px;
                padding: 40px 35px 40px 40px;
                transition: all 1s;
                .row{
                    span{
                        width: auto;
                    }
                }
                .row-end{
                    display: flex;
                    justify-content: end;
                }
                .title{
                    font-size: 14px;
                    font-weight: 600;
                    line-height: 17px;
                    width: 100%;
                    max-width: 354px;
                    margin: 0;
                }
                .row-description{
                    padding-top: 15px;
                    display: flex;
                    gap: 24px;
                    flex-direction: column;
                    .element{
                        transition: all 1s;
                        width: 100%;
                    }
                    .element.active{
                        padding: 16px 52px 16px 40px;
                        border-radius: 16px;
                        background-color: #4610ad;
                        width: 610px;
                        transform: translate(-112px, 0px);
                    }
                }
            }
        }
    }

    .features-before1{
        position: absolute;
        top: 0;
        left: 10%;
        transform: translateX(40px);
    }
    .features-before2{
        position: absolute;
        bottom: 0;
        right: 0;
    }
}