.start-mining{
    padding: 88px 0;
    background: linear-gradient(180.00deg, rgb(43, 7, 110),rgb(13, 13, 43) 100%);
    .base{
        width: 100%;
        max-width: 100%;
        padding: 48px;
        background-color: #3671e9;
        background-image: url(assets/img/start-mining-now.svg);
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 16px;
        .row{
            justify-content: space-between;
        }
        .text-content{
            max-width: 501px;
            .title{
                margin-bottom: 16px;
            }
            .subtitle{
                max-width: 348px;
                margin: 0;
            }
        }
        .email{
            max-width: 590px;
            margin: 28px 0 32px;
            .row-input{
                padding: 0 12px;
                justify-content: space-between;
            }
            .input-email{
                max-width: 374px;
                padding: 0;
                outline: none;
                border: none;
                background: none;
                border-bottom: 2px solid #ffffff66;
                color: #fff;
            }
            .input-email::placeholder{
                color: #fff;
            }
            .btn{
                padding: 13px 32px;
                max-width: 152px;
                background-color: #fff;
                color: black;
            }
            .btn:hover{
                background: #3671E9;
                color: #fff;
            }
        }
    }


    .message-page{
        transform: translateX(50px);
        opacity: 0;
        overflow: hidden;     /*!important*/
        position: fixed;
        max-width: 500px;
        width: 100%;
        z-index: -5;
        top: 12px;
        right: 12px;
        border-radius: 16px;
        background-color: #fff;
        transition: all 1s;
        .modal-container{
            padding: 12px 16px;
            height: 70px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .title-modal{
                font-size: 18px;
            }
        }
    }
    .message-page.active{
        z-index: 2;
        opacity: 1;
        transform: translateX(0);
    }
}