@media screen and (max-width: 767px) {
    .first-screen{
        .col{
            padding-top: 40px;
        }
        .first-screen_img{
            svg{
                transform: translate(50%, 0px);
                right: 50%;
                bottom: -700%;
            }
        }
    }

    .choice{
        .statistics-company{
            padding-bottom: 40px;
            padding-top: 100px;
            .info-statistics_container{
                flex-wrap: wrap;
                .info-statistics{
                    min-width: 200px;
                    text-align: center;
                }
            }
        }
    }
    .cryptocurrencies {
        padding-top: 430px;
        .row{
            .slick-list{
                width: 370px;
            }
        }
    }
    .start-mining{
        .base{
            .email{
                .row-input{
                    gap: 25px;
                    justify-content: center;
                }
            }
        }
        .message-page{
            right: 50%;
            transform: translatex(50%);
            top: -12px;
        }
        .message-page.active{
            top: 12px;
            transform: translatex(50%);
        }
    }
}
