@media screen and (max-width: 1200px){
    .features{
        .title{
            margin-bottom: 20px;
        }
        .subtitle{
            margin-bottom: 30px;
        }
        .invest{
            .row{
                gap: 50px;
                justify-content: center;
                .text-content{
                    text-align: center;
                }
            }
            .chart{
                .subimg_content{
                    justify-content: start;
                }
            }
        }

        .detailed-statistics{
            .row{
                justify-content: center;
            }
            .text-content{
                text-align: center;
            }
            .img-container{
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .grow-profit{
            .row{
                justify-content: center;
            }
            .text-content{
                text-align: center;
            }
            .img-container{
                padding-top: 40px;
            }
        }
    }

    .start-mining{
        .base{
            .row{
                justify-content: center;
            }
            .text-content {
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
    }
}