.cryptocurrencies{
    background-color: #fff;
    padding-top: 317px;
    .container{
        max-width: 966px;
    }
    .page-title{
        color: #000;
        max-width: 741px;
        margin: 0 auto;
        text-align: center;
        margin-bottom: 64px;
    }

    .row{
        flex-wrap: nowrap;
        padding: 0 12px;
        .slick-list{
            width: 1200px;
            margin: 0 auto;
        }
    }
    .card{
        width: 370px;
        height: 433px;
        margin-right: 45px;
        border-radius: 16px;
        margin-bottom: 100px;
        transition: all 0.1s;
        .card-img{
            max-width: 80px;
            margin: 47px auto 48px;
        }
        .card-content{
            /*display: flex;
            flex-direction: column;
            justify-content: center;*/
            color: #000;
            text-align: center;
        }
        .card-tile{
            display: flex;
            align-items: center;
            gap: 10px;
            margin: 0 auto 19px;
            max-width: 160px;
            font-size: 32px;
            font-weight: 700;
            line-height: 150%;
            span{
                color: #BDBDBD;
                opacity: 1;
                text-align: center;
                font-size: 18px;
                font-weight: 500;
                line-height: 150%;
            }
        }
        .card-subtitle{
            color: #828282;
            margin-bottom: 25px;
            line-height: 175%;
            letter-spacing: 0.16px;
        }
        .card-btn{
            background-color: #fff;
            max-height: 64px;
            max-width: 64px;
            padding: 0;
            justify-content: center;
            height: 100%;
            width: 100%;
            border: 1px solid #D5CDE2;
            transition: all 0.1s;
            text-wrap: nowrap;
            p{
                margin: 0;
            }
            svg{
                height: 64px;
                width: 64px;
            }
        }
        .card-btn:hover{
            svg{
                circle{
                    fill: #3671E9;
                }
                path{
                    fill: #fff;
                }
            }
        }
    }
    .card.active{
        background-color: #2B076E;
        .card-content{
            color: var(--text-color);
        }
        .card-img{
            animation-name: img-card_active;
            animation-duration: 2s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;
            @keyframes img-card_active {
                0%{
                    transform: translate(0, 0);
                }
                25%{
                    transform: translate(0, 5px);
                }
                50%{
                    transform: translate(0, 0);
                }
                75%{
                    transform: translate(0, -5px);
                }
                100%{
                    transform: translate(0, 0);
                }
            }
        }
        .card-tile{
            span{
                color: #fff;
                opacity: 0.7;
            }
        }
        .card-subtitle{
            color: #fff;
        }
        .card-btn{
            background-color: #3671E9;
            border: none;
            max-width: 204px;
            transform: translate(2px, 0px);
            max-height: 65px;
            width: 100%;
            padding: 17px 15px 16px 23px;
            align-items: center;
            justify-content: space-between;
            p{
                font-size: 18px;
                font-weight: 500;
                line-height: 150%;
            }
            svg{
                width: 32px;
                height: 32px;
            }
        }
        .card-btn:hover{
            background-color: #fff;
            svg{
                circle{
                    fill: #3671E9;
                }
                path{
                    fill: #fff;
                }
            }
        }
    }
}