.earn{
    z-index: 1;
    position: relative;
    padding-top: 168px;
    padding-bottom: 155px;
        .container{
            max-width: 966px;
        }
        .container-earn{
            text-align: center;
            color: var(--text-color);
            padding-bottom: 63px;
            .page-title{
                margin-bottom: 24px;
            }
            .subtitle{
                padding-right: 15px;
                margin: 0 auto;
                max-width: 622px;
                font-size: 18px;
                font-weight: 400;
                line-height: 150%;
            }
        }
        .calculate-container{
            position: absolute;
            transform: translateX(-50%);
            left: 50%;
            right: auto;
            max-width: 942px;
            max-height: 371px;
            width: 100%;
        }
        .calculate{
            background-color: #FBFCFE;
            border-radius: 16px;
            box-shadow: 0px 5px 20px 0px #1d0a37;
            padding: 48px;
            .col-container{
                display: flex;
                gap: 97px;
                flex-direction: column;
                .row{
                    justify-content: center;
                    gap: 48px;
                    padding: 0 12px;
                }
                .input-default{
                    border: none;
                    border-bottom: 1px solid #E0E0E0;
                    height: 41px;
                    border-radius: 0;
                }
                .input-default:focus{
                    outline: none;
                    box-shadow: none;
                    border: none;
                    border-bottom: 2px solid #000;
                }
                .input-text{
                    max-width: 403px;
                    margin-top: 10px;
                    padding: 0;
                }
                .input-text::placeholder{
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 150%;
                    color: #000;
                }
                .form-select{
                    background-image: url(assets/img/arrow-down_input.svg);
                    background-size: auto;
                    background-position: right 9px;
                    position: relative;
                    max-width: 200px;
                    margin-top: 10px;
                    padding: 0;
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 150%;
                    cursor: pointer;
                    span{
                        padding-left: 7px;
                    }
                    .list{
                        list-style: none;
                        padding: 0;
                        display: none;
                        margin-top: 11px;
                        background-color: #f9f9f9;
                        border-radius: 10px;
                        li{
                            cursor: pointer;
                        }
                    }
                }
                .open{
                    .list{
                        display: block;
                        list-style: none;
                        padding: 0;
                    }
                }

                .btn-calculate{
                    max-width: 147px;
                    padding: 15px 32px;
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 150%; 
                }

                .hour-revenue{
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 150%;
                    letter-spacing: 0.64px;
                    text-transform: uppercase;
                    margin-bottom: 16px;
                    color: #3671E9;
                }
                .revenue{
                    font-size: 32px;
                    font-weight: 700;
                    line-height: 150%;
                    letter-spacing: 1.28px;
                    margin-bottom: 4px;
                    display: inline-block;
                    span{
                        color: #3671E9;
                        padding-left: 10px;
                    }
                }
                .revenue-description{
                    color: #828282;
                    line-height: 175%;
                    letter-spacing: 0.09px;
                    margin: 0;
                }
            }
        }
}