@media screen and (max-width: 991px) {
    header{
        .nav-list{
            margin: 0 auto;
            padding: 12px;
        }
        .nav-account{
            margin: 10px auto 0;
        }
    }

    .first-screen{
        .first-screen_action{
            .base{
                height: auto;
                margin: 0 auto;
                max-width: 500px;
                .saving{
                    text-wrap: wrap;
                }
                .time-saving{
                    padding-left: 8px;
                    text-wrap: wrap;
                }
            }
        }

        .first-screen_title{
            font-size: 42px;
            margin-top: 10px;
        }

        .first-screen_img{
            svg{
                width: 300px;
                height: 300px;
                bottom: 250%;
            }
        }
    }
    .choice{
        .statistics-company{
            .info-statistics_container{
                flex-wrap: nowrap;
                justify-content: center;
                gap: 0;
            }
            .info-statistics{
                max-width: 275px;
                .row{
                    justify-content: center;
                }
            }
        }
        .choice-container_content{
            z-index: 2;
            min-width: 320px;
        }
        .container-page_choice{
            gap: 0;
            justify-content: center;
        }




        .img-container_choice{
            z-index: 2;
            img{
                transform: translate(-161px, -40px);
                width: 400px;
                height: auto;
            }
        }
    }

    .earn{
        .calculate-container{
            padding: 0 24px;
        }
        .calculate{
            .col-container{
                gap: 20px;
            }
        }
    }

    .cryptocurrencies{
        padding-bottom: 70px;
        .row{
            .slick-list{
                width: 785px;
                margin: 0 auto;
            }
        }
        .card{
            margin-right: 45px;
            margin-bottom: 30px;
        }
    }
    .features{
        
        .detailed-statistics{
            .img-container{
                img{
                    left: 0;
                    padding: 0 12px;
                }
            }
            .text-content{
                padding-top: 30px;
            }
        }
        .grow-profit{
            
            .img-container{
                .base{
                    padding: 40px 24px 40px 24px;
                    .row-description{
                        .element{
                            svg{
                                width: 100%;
                            }
                        }
                        .element.active{
                            padding: 12px;
                            transform: translate(0);
                            max-width: 610px;
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

    .start-mining{
        .base{
            
        }
    }
}