*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Rubik, sans-serif;
}
body{
    background-color: var(--text-color-dark);
    overflow-x: hidden;
}
a{
    text-decoration: none;
}
.container-xxl{
    max-width: 1226px;
    padding-left: 13px;
}
.btn-default{
    box-shadow: none;
    display: inline-flex;
    align-items: center;
    border-radius: 32px;
    background: #3671E9;
    color: var(--text-color);
    cursor: pointer;
    font-size: 18px;
    padding: 15px 32px;
    font-weight: 500;
    line-height: 150%;
}
.btn-default:hover{
    background-color: #fff;
    color: black;
}

ul.slick-dots {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    gap: 15px;

    li {
        display: inline-block;
        font-size: 0;
        
        &:before{
            display: none;
        }
        
        &.slick-active {
            button {
                background-color: #3671E9;
            }
        }
    }
    button {
        font-size: 0;
        padding: 0px;
        border-radius: 50%;
        opacity: 0.9;
        border: 2px solid #3671E9;
        width: 12px;
        height: 12px;
        background: transparent;
        outline: none;
    }
}

.page-title{
    color: var(--text-color);
    font-size: 40px;
    font-weight: 700;
    line-height: 150%;
}
.title{
    color: var(--text-color);
    font-size: 32px;
    font-weight: 700;
    line-height: 150%;
}
.subtitle{
    color: var(--text-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 175%;
    letter-spacing: 0.16px;
    margin-bottom: 32px;
}
.input-default {
    border: none;
    border-bottom: 1px solid #E0E0E0;
    height: 41px;
    border-radius: 0;
}